import {AbstractControl} from '@angular/forms';

export class PasswordValidation {

    static MatchPassword(AC: AbstractControl) {
       let enterPasswd = AC.get('enterPasswd').value; // to get value in input tag
       let cnfrmPasswd = AC.get('cnfrmPasswd').value; // to get value in input tag
        if(enterPasswd != cnfrmPasswd) {
           AC.get('cnfrmPasswd').setErrors( {MatchPassword: true} )
        } else {
            return null
        }
    }
}


