import { Component} from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { PasswordValidation } from './password-validation';
import { Router } from '@angular/router';

@Component({
  moduleId: module.id,
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent {
	registerForm : FormGroup;

  constructor(private router: Router, private fb: FormBuilder) {
this.registerForm = fb.group({
  	  'name' : ['', Validators.compose([Validators.required, Validators.maxLength(25)])],
	  'email' : ['', Validators.compose([Validators.required])],
	  'enterPasswd' : ['', Validators.compose([Validators.required, Validators.maxLength(20), Validators.minLength(5)])],
	  'cnfrmPasswd' : ['', Validators.compose([Validators.required])]
	},{
	validator: PasswordValidation.MatchPassword
	})
	
   }


}







